.note {
  max-width: 10rem;
  max-height: 15rem;
  padding: 0 0.5rem 0.5rem 0.5rem;
  margin: 1rem;
  border-radius: 0.5rem;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  overflow: hidden;
  overflow-y: scroll;
  &-unchecked{
  background-color: #d0bfff;
  }  
  &-checked{
  background-color: #12b886;
  }

  // &-paragraph {
  //   width: 90%;
  // }
  &::-webkit-scrollbar {
    width: 0;
  }


}




