.container {
  height: 60rem;
  width: 100%;
  background-color: #dbe4ff;
  // display: flex;
  // justify-content: center;

  &-list {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    list-style-type: none;
    overflow-y: scroll;
    max-height: 50rem;
    &::-webkit-scrollbar {
      width: 0;
    }
  }

}
